import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CardActions from '@material-ui/core/CardActions';
import Dropzone from 'react-dropzone'
import CircularProgress from '@material-ui/core/CircularProgress';
import Markdown from './Markdown'
import { predictCaptcha } from '../api/models'

const styles = theme => ({
    paper: {
        backgroundColor: theme.palette.grey[800],
        color: theme.palette.common.white,
        marginBottom: theme.spacing.unit * 4,
    },
    demo: {
        height: "100%",
        backgroundColor: 'inherit',
        paddingLeft: `${theme.spacing.unit * 6}px`,
        paddingRight: `${theme.spacing.unit * 6}px`,
        paddingTop: `${theme.spacing.unit * 3}px`,
        paddingBottom: `${theme.spacing.unit * 3}px`,
        [theme.breakpoints.down(600)]: {
            width: "100%",
            paddingTop: `${theme.spacing.unit * 1}px`,
            paddingBottom: `${theme.spacing.unit * 1}px`,
            paddingLeft: `${theme.spacing.unit * 3}px`,
            paddingRight: `${theme.spacing.unit * 3}px`,
        },
    },
    captchaTitle: {
        [theme.breakpoints.down(600)]: {
            padding: `${theme.spacing.unit * 1}px`,
        }
    },
    captchaTitleText: {
        fontFamily: "BLACK",
        fontSize: 28,
        textAlign: 'center',
        [theme.breakpoints.down(600)]: {
            fontFamily: "Roboto",
            fontSize: 18,
        },
    },
    media: {
        height: 0,
        paddingTop: '37.5%',
        [theme.breakpoints.down(600)]: {
            paddingTop: '37.5%',
        },
    },
    actions: {
        display: 'flex'
    },
    uploadButton: {
        margin: `${theme.spacing.unit * 3}px`,
        [theme.breakpoints.down(600)]: {
            fontSize: "60%",
            margin: `${theme.spacing.unit * 1}px 0`,
        },
    },
    predict: {
        margin: `${theme.spacing.unit * 3}px`,
        [theme.breakpoints.down(600)]: {
            margin: `${theme.spacing.unit * 1}px ${theme.spacing.unit * 2.9}px`,
        },
    },
    predictText: {
        fontFamily: "Monospace",
    },
    loadingProgress: {
        color: '#e0e0e0',
        margin: `${theme.spacing.unit * 3}px`,
        [theme.breakpoints.down(600)]: {
            fontSize: "40%",
            margin: `${theme.spacing.unit * 1}px 0`,
        },
    },
    summary: {
        height: "100%",
        color: theme.palette.grey[800],
        backgroundColor: theme.palette.common.white,
        paddingLeft: `${theme.spacing.unit * 6}px`,
        paddingRight: `${theme.spacing.unit * 6}px`,
        paddingTop: `${theme.spacing.unit * 3}px`,
        paddingBottom: `${theme.spacing.unit * 3}px`,
        [theme.breakpoints.down(600)]: {
            paddingTop: `${theme.spacing.unit * 1}px`,
            paddingBottom: `${theme.spacing.unit * 1}px`,
            paddingLeft: `${theme.spacing.unit * 2}px`,
            paddingRight: `${theme.spacing.unit * 2}px`,
        },
    },
})

const summary = `
## Model
- [WRN-12-2](https://arxiv.org/abs/1605.07146)
- [SE module](https://arxiv.org/abs/1709.01507)
- 4-way 62-class softmax (0-9A-Za-z)
- 93.85% test accuracy with 358,824 params

## Dataset
- images generated by [captcha](https://github.com/lepture/captcha)
- fonts: Microsoft YaHei, SimHei, Times New Roman, Computer Modern
- font sizes: 30, 34, 38, 42, 46, 50
- random dots and curves

## *DOES NOT WORK ON OTHER DATASETS*
`

const Demo = withStyles(styles)(({ classes, className }) => {
    const [files, setFiles] = useState([{ preview: process.env.PUBLIC_URL + '/img/c1.jpg' }])
    const [predict, setPredict] = useState('AKi7')
    const [loading, setLoading] = useState(false)

    const onDrop = (files) => {
        const file = files[0]
        Object.assign(file, {
            preview: URL.createObjectURL(file)
        })
        setFiles([file])
        setLoading(true)
        predictCaptcha(file)
            .then(p => {
                setPredict(p)
                setLoading(false)
            })
    }
    return (
        <Paper className={classes.paper}>
            <Grid container>
                <Grid item md={6} sm={12}>
                    <Card className={classes.demo}>
                        <CardContent className={classes.captchaTitle}>
                            <Typography component="h1" variant="h4" color="inherit" gutterBottom>
                                <div className={classes.captchaTitleText}>
                                    Captcha Recognation
                                </div>
                            </Typography>
                        </CardContent>
                        <CardMedia
                            title="CAPTCHA image"
                            image={files[0].preview}
                            className={classes.media}
                        >
                        </CardMedia>
                        <CardActions className={classes.actions} disableActionSpacing>
                            <Grid container>
                                <Grid item md={4} sm={4}>
                                    <Dropzone
                                        multiple={false}
                                        accept="image/*"
                                        onDrop={onDrop}
                                        disableClick={true}
                                    >
                                        {({ getRootProps, getInputProps, open }) => (
                                            <div {...getRootProps()}>
                                                <input {...getInputProps()} />
                                                <Button
                                                    variant="contained"
                                                    color="default"
                                                    className={classes.uploadButton}
                                                    onClick={open}
                                                >
                                                    Upload
                                        <CloudUploadIcon />
                                                </Button>
                                            </div>
                                        )}
                                    </Dropzone>
                                </Grid>
                                <Grid item md={6} sm={6} align='center'>
                                    <Typography
                                        className={classes.predict}
                                        component="h1"
                                        variant="h4"
                                        color="inherit"
                                    >
                                        <div className={classes.predictText}>
                                            {predict}
                                        </div>
                                    </Typography>
                                </Grid>
                                <Grid item md={2} sm={2}>
                                    {loading ? (
                                        <CircularProgress
                                            className={classes.loadingProgress}
                                        />) : (
                                            <CircularProgress
                                                variant="static"
                                                className={classes.loadingProgress}
                                                value={100}
                                            />
                                        )}
                                </Grid>
                            </Grid>
                        </CardActions>
                    </Card>
                </Grid>

                <Grid item md={6} sm={12}>
                    <Card className={classes.summary}>
                        <CardContent>
                            <Markdown className={classes.markdown}>
                                {summary}
                            </Markdown>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Paper >
    )
})

export default withStyles(styles)(Demo)